import React from 'react';
import { AuthContext } from "Boot/auth/AuthProvider";
import BaseIndexPage from "components/HOC/BaseIndexPage";
import UserHeader from "components/section-components/UserHeader";
import { useContext, useEffect, useState } from "react";

import { PROPERTY } from "components/Routes/Constants";
import { connect } from "react-redux";
import { Link, Redirect, Route, useParams, withRouter } from "react-router-dom";
import { Card, CardBody, CardTitle, Col, Container, Row } from "reactstrap";
import FormWrapper from "./Components/FormWrapper";

const Index = ({ turnOffSubmitting, turnOnSubmitting, match, ...props }) => {
  const { changePass, isLoggedIn } = useContext(AuthContext);

  const [loading, setLoading] = useState(true);
  const [token, setToken] = useState("");
  const { tokenId } = useParams();

  useEffect(() => {
    setToken(tokenId);
  }, [tokenId]);

  if (isLoggedIn)
    return (
      <Route>
        <Redirect to={`${PROPERTY}/create#step1`} />
      </Route>
    );

  if (loading === undefined)
    return (
      <div className="signin">
        <Container>
          <UserHeader className="py-4" />
          <Row className="d-flex justify-content-center my-4">
            <Col md="5">
              <Card className="border-0 card-shadow-info">
                <CardBody>
                  <div class="fa-3x text-center py-5">
                    <i class="fas fa-spinner fa-spin "></i>
                  </div>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  else if (loading === false)
    return (
      <div className="signin">
        <Container>
          <UserHeader className="py-4" />
          <Row className="d-flex justify-content-center my-4">
            <Col md="5">
              <Card className="border-0 card-shadow-info">
                <CardTitle></CardTitle>
                <CardBody>
                  <p class="mb-5">
                    Your Request has been expired please go back to your login
                    page.
                  </p>
                  <p className="text-right">
                    <Link to="/login" className="btn  btn-blue">
                      go back
                    </Link>
                  </p>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  return (
    <div className="signin">
      <Container>
        <UserHeader className="py-4" />
        <Row className="d-flex justify-content-center my-4">
          <Col md="5">
            <Card className="border-0 card-shadow-info">
              <CardTitle className="text-center">
                <h2 className="sub-title mt-3 pl-4 mb-0"> Change Password</h2>
                <p class="pl-4 small text-muted">Enter email to recieve link</p>
              </CardTitle>
              <CardBody>
                <FormWrapper
                  {...props}
                  onSubmit={(values) =>
                    changePass(
                      { token, ...values },
                      turnOffSubmitting,
                      turnOnSubmitting
                    )
                  }
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};
const mapStateToProps = (state, props) => ({
  noBanner: true,
  isHideFooter: true,
  noHeader: true,
  isHideNavigation: true,
});

export default connect(mapStateToProps, null)(BaseIndexPage(withRouter(Index)));
